@import url(https://fonts.googleapis.com/css2?family=Hind+Siliguri&family=Quicksand:wght@500&display=swap);
* {
  display: border-box;
  margin: 0, auto;
  font-family: 'Quicksand', sans-serif;
}

body {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100vh;
  background-image: url('https://wallpaperaccess.com/full/24936.jpg');
  background-size: cover;
}

.container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  border-radius: 10px;
  padding: 15px;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.title {
  text-align: center;
  margin-top: 0;
}

.cards {
  display: -webkit-flex;
  display: flex;
  margin: 20px;
  -webkit-align-items: center;
          align-items: center;
}

.img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

.info {
  margin-left: 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.name {
  margin-bottom: 0;
}

.type {
  margin-top: 0;

  color: rgba(107, 103, 103, 0.9);
}

.btn {
  background-color: rgba(252, 7, 3, 0.8);
  border: none;
  border-radius: 15px;
  padding: 5px;
  color: white;
  margin-top: 20px;
}

